ológicos en frutos y vegetales, tales como: la desaparición de astringencia, sabor agrio, cambios en acidez, síntesis de pigmentos con eliminación de clorofila, ablandamiento de tejidos por degradación de la pectina y desarrollo de aromas y sabores.")]),_c('p',{staticClass:"text-justify"},[_vm._v("Estos cambios en las frutas y verduras, permiten obtener el sabor natural de los frutos y las verduras cultivadas. ")]),_c('p',{staticClass:"text-justify"},[_vm._v("Además, la actividad respiratoria de estos permite clasificar los frutos en dos categorías.")])])])]),_c('div',{staticClass:"px-5 py-3",attrs:{"titulo":"Frutos climatéricos"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img14.png"),"alt":""}})])]),_c('div',{staticClass:"col-md-7"},[_c('p',{staticClass:"text-justify"},[_vm._v("Las especies climatéricas tienen el aumento de la síntesis de etileno, el cual actúa como responsable del inicio de la maduración y dota al fruto de un mayor potencial para su regulación y de la posibilidad de continuar el proceso una vez recolectado, siempre y cuando haya adquirido la “capacidad para madurar”. ")]),_c('p',{staticClass:"text-justify"},[_vm._v("Este comportamiento permite flexibilizar su recolección, ya que se puede realizar durante cierto tiempo, en función de que su comercialización se efectúe inmediatamente, o tras la conservación frigorífica. ")])])])]),_c('div',{staticClass:"px-5 py-3",attrs:{"titulo":"Frutos no climatéricos"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img15.png"),"alt":""}})])]),_c('div',{staticClass:"col-md-7 mt-5"},[_c('p',{staticClass:"text-justify"},[_vm._v("Estos no producen una actuación en su madurez debido a que carecen de tal capacidad. Deben recolectarse cuando han alcanzado su tiempo establecido y la calidad para consumo. ")])])])])]),_vm._m(5),_c('div',